:root[data-theme="light"] {
  --gray-200: #d8dbdf;
  --base-white: #fff;
  --base-white-2: #fdfdfd;
  --neutral-900: #262626;
}
:root[data-theme="dark"] {
  --gray-200: #383B42;
  --base-white: #262626;
  --base-white-2: #1a1a1a;
  --neutral-900: #FCFCFC;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--base-white-2);
  color: var(--neutral-900);
}

.vr {
  width: 1px;
  margin: 5px 10px;
  border-right: 1px solid var(--neutral-900);
}

